import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { ApiService } from 'src/app/common/api-service/api.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-b1-list-work',
  templateUrl: './b1-list-work.component.html',
  styleUrls: ['./b1-list-work.component.scss']
})
export class B1ListWorkComponent implements OnInit {

  subscription: Subscription[] = [];

  // data for comboBox
  typeOfWork: any[] = [
    { value: '0', viewValue: 'Loại hình' },
    { value: '1', viewValue: 'Toàn thời gian cố định' },
    { value: '2', viewValue: 'Toàn thời gian tạm thời' },
    { value: '3', viewValue: 'Bán thòi gian cố định' },
    { value: '4', viewValue: 'Bán thòi gian tạm thời' },
    { value: '5', viewValue: 'Theo hợp đồng / tư vấn' },
    { value: '6', viewValue: 'Thực tập' },
    { value: '7', viewValue: 'Khác' },
  ];

  expYears: any[] = [
    { value: "none", viewValue: "Kinh nghiệm" },
    { value: '1', viewValue: 'Chưa có kinh nghiệm' },
    { value: '2', viewValue: 'Dưới 1 năm' },
    { value: '3', viewValue: '1 năm' },
    { value: '4', viewValue: '2 năm' },
    { value: '5', viewValue: '3 năm' },
    { value: '6', viewValue: '4 năm' },
    { value: '7', viewValue: '5 năm' },
    { value: '8', viewValue: 'Trên 5 năm' },
  ];

  sexs: any[] = [
    { value: "0", viewValue: "Nam" },
    { value: "1", viewValue: "Nữ" },
    { value: "none", viewValue: "Giới tính" }
  ];

  salaries: any[] = [
    { value: "none", viewValue: "Mức lương" },
    { value: "1", viewValue: "1-3 triệu" },
    { value: "2", viewValue: "3-5 triệu" },
    { value: "3", viewValue: "5-7 triệu" },
    { value: "4", viewValue: "7-10 triệu" },
    { value: "5", viewValue: "10-12 triệu" },
    { value: "6", viewValue: "12-15 triệu" },
    { value: "7", viewValue: "15-20 triệu" },
    { value: "8", viewValue: "20-25 triệu" },
    { value: "9", viewValue: "25-30 triệu" },
    { value: "10", viewValue: "30 triệu trở lên" },
  ];

  // binding data
  listCareer: any;

  listCity: any;

  listJob: any;

  listBannerAds: any;

  listJobNew: any = [];

  //  binding search
  // career search
  career: string = '0'

  // city search
  city: string = '0';

  // salary search
  salary: string = 'none';

  // exp search
  exp: string = 'none';

  // typeofwork search
  typeofwork: string = 'none';

  // sex search
  sex: string = 'none';

  // keyword search
  keyword: string;

  constructor(private api: ApiService, private activatedRoute: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    // get Career
    this.getCareer();

    // get City
    this.getCity();

    //listHotNew
    this.getJobNew();

    this.activatedRoute.params.subscribe(params => {
      console.log(params);
      this.career = params['career'];
      this.city = params['city'];
      this.salary = params['salary'];
      this.exp = params['exp'];
      this.typeofwork = params['typeofwork'];
      this.sex = params['sex'];
      this.keyword = params['keyword'];
    });

    // on Filter Job
    this.onFilterJob();

    // get Banner Ads
    this.getBannerAds();
  }

  /**
   * on Filter Job
   */
  onFilterJob() {
    const param = {
      career: this.career,
      city: this.city,
      salary: this.salary,
      exp: this.exp,
      typeofwork: this.typeofwork,
      sex: this.sex,
      keyword: this.keyword,
    }

    this.subscription.push(this.api.excuteAllByWhat(param, '13').subscribe(data => {
      // set data for listJob
      console.log("tada<<<<<<", data);

      data[0].salary = data[0]['salary'] / 1000000
      this.listJob = data;
    }))
  }

  /**
  * get Career
  */
  getCareer() {
    const param = {};
    this.subscription.push(this.api.excuteAllByWhat(param, '130').subscribe(data => {
      if (data.length > 0) {

        let temp = [
          {
            id: "0",
            name: "Ngành nghề"
          }
        ]
        data.forEach(item => {
          temp.push(item);
        });

        // set data for listCareer	
        this.listCareer = temp;
      } else {
        this.listCareer = 0;
      }
    }));
  }

  /**
  * get City
  */
  getCity() {
    const param = {}
    this.subscription.push(this.api.excuteAllByWhat(param, '200').subscribe(data => {
      if (data.length > 0) {

        let temp = [
          {
            id: "0",
            name: "Địa điểm"
          }
        ]
        data.forEach(item => {
          temp.push(item);
        });
        // set data for city	
        this.listCity = temp;
      }
    }))
  }

  /**
  * get Banner Ads
  */
  getBannerAds() {
    const param = {}
    this.subscription.push(this.api.excuteAllByWhat(param, '69').subscribe(data => {
      if (data.length > 0) {
        // set data for city	
        this.listBannerAds = data;
        console.log('listbanner', this.listBannerAds);

      }
    }))
  }

  /**
  * get Job New
  */
  getJobNew() {
    const param = {}
    this.subscription.push(this.api.excuteAllByWhat(param, '18').subscribe(data => {
      if (data.length > 0) {
        // set data for list jog hot	
        let colSlide = [];
        let processList = [
          {
            rowSlide: [
              colSlide = [data[0], data[1], data[2], data[3],data[4],data[5]],
            ],
            active: true
          },
          {
            rowSlide: [
              colSlide = [data[6], data[7], data[8], data[9],data[10],data[11]],
            ],
            active: false
          }
        ];

        this.listJobNew = processList;
        console.log(this.listJobNew);
      }
    }))
  }

  onDetailJobClick(idjob) {
    const url = '/b2-detail-work/' + idjob;
    this.router.navigate([url]);
    console.log(url)
  }

}
